      }

      this.$showLoading();
      var params = {};

      if (item.platform == "1") {
        this.activePlatform = "shopify";
        params = {
          shopUrl: item.subShopUrl,
          getAuth: 1,
          fromList: 1,
          platform: item.platform
        };
