        if (!this.shopifyUrl) {
          this.$elementMessage("Please enter Shopify store URL", "error");
          return;
        }

        params = {
          shopUrl: this.shopifyUrl,
          platform: 1
        };
      } else if (this.activePlatform == "woo") {
        if (!this.shopifyUrl) {
          this.$elementMessage("Please enter Woocommerce store URL", "error");
          return;
        }

        params = {
