            _this15.shopifyWindow = window.open(r.Data.Results.url);

            _this15.connectConfirmStart(state);
          }

          if (_this15.activePlatform == "woo") {
            _this15.getItem();

            _this15.connectStoreOpen = false;

            _this15.$elementMessage("add a new store success", "success");
          }
        } else {
          _this15.connectStoreLoading = false;

          _this15.$elementMessage(r.Message, "error");
        }
      });
